      // 
      getMapImg(e, id) {
        // svgcanvas
        let nodesToRecover = [];
        let nodesToRemove = [];
        let svgElem = $("#container").find("svg"); // divReportdomid
        let prepare = [];
        let that = this;
        for (let i = 0; i < svgElem.length; i++) {
          prepare.push(0);
        }
        svgElem.each(function (index, node) {
          let parentNode = node.parentNode;
          let svgImg = node.outerHTML;
          let canvas = document.createElement("canvas");
          canvg(canvas, svgImg);
          canvas.style.position = node.style.position;
          canvas.style.width = node.style.width;
          canvas.style.height = node.style.height;
          canvas.style.left = 0;
          canvas.style.top = 0;
          let ctx = canvas.getContext("2d");
          let svg_xml = new XMLSerializer().serializeToString(node);
          let img = new Image();
          img.src = "data:image/svg+xml;base64," + window.btoa(svg_xml);
          img.onload = function () {
            ctx.drawImage(img, 0, 0);
            prepare[index] = 1;
            download(canvas.toDataURL("image/png")); // 
          };
          parentNode.appendChild(canvas); // canvassvg
          nodesToRemove.push({
            // canvas
            parent: parentNode,
            child: canvas
          });
          // setTimeout(function () {
          //     nodesToRemove.push({
          //       // canvas
          //       parent: parentNode,
          //       child: canvas
          //     });
          //   },1000)
          // nodesToRecover.push({
          //   // svg
          //   parent: parentNode,
          //   child: node
          // });
          // parentNode.removeChild(node); // svg
        });
        let waitInterval = setInterval(() => {
          let isComplete = true;
          for (let i = 0; i < prepare.length; i++) {
            if (prepare[i] == 0) {
              isComplete = false;
              break;
            }
          }
          if (isComplete) {
            clearInterval(waitInterval);
            // divcanvas
            html2canvas(document.getElementById("container"), {
              useCORS: true,
              scale: 0.5,
              backgroundColor: null
            }).then(cnv => {
              console.log(that.$refs.imageDemo);
              console.log(cnv);
              let imgUrl = cnv.toDataURL("image/png"); // canvasimgsrcbase64
              console.log(imgUrl);
              that.screenshotImg = imgUrl;
              // 
              if (e == 1) {
                that.businessHotImage = imgUrl;
                // 
                let data = JSON.stringify({
                  userId: that.userInfo.userId,
                  token: that.userInfo.token,
                  reportId: id,
                  thermodynamicChart: imgUrl
                });
                updThermodynamicChart(data).then(res => {
                  console.log(res);
                });
              }
              // for (let i = 0; i < nodesToRecover.length; i++) {
              //   nodesToRecover[i].parent.appendChild(nodesToRecover[i].child);
              // }
              for (let i = 0; i < nodesToRemove.length; i++) {
                nodesToRemove[i].parent.removeChild(nodesToRemove[i].child);
              }
            });
          }
        }, 300);
      },
