      // 
      drawCircle(e, range) {
        console.log(e);
        let map = this.map;
        let that = this;
        map.removeOverlay(this.circle);
        map.removeOverlay(this.labels);
        for (let i = 0; i < e.length; i++) {
          let point = new BMap.Point(e[i].lng, e[i].lat);
          let opt = {
            strokeColor: "#027AFF",
            fillColor: "#027AFF",
            fillOpacity: "0.5"
          };
          var circle = new BMap.Circle(point, range, opt);
          that.circle.push(circle);
          var opts = {
            position: point, // 
            offset:
              // range == 7000 ? new BMap.Size(-20, -20) : new BMap.Size(-30, -55) //
              range == 3000 ? new BMap.Size(-25, -40) : new BMap.Size(-20, -20) //
          };
          var labels = new BMap.Label(
            e[i].address + "<br> " + e[i].area + "",
            opts
          ); // 
          that.labels.push(labels);
          labels.setStyle({
            color: "#ffffff",
            backgroundColor: "transparent", //
            borderColor: "transparent", //
            // fontSize: "12px",
            fontSize: range == 3000 ? "16px" : "12px",
            height: "20px",
            fontWeight: 600,
            textAlign: "center",
            lineHeight: range == 3000 ? "40px" : "20px",
            // lineHeight: "20px",
            fontFamily: ""
          });
          map.addOverlay(circle);
          map.addOverlay(labels);
        }
      },
