          var count = rs.boundaries.length; //
          if (count === 0) {
            alert("");
            return;
          }
          var pointArray = [];
          for (var i = 0; i < count; i++) {
            var ply = new BMap.Polygon(rs.boundaries[i], {
              strokeWeight: 2,
              strokeColor: "#ff0000",
              fillColor: "",
              fillOpacity: 0
            }); //
            map.addOverlay(ply); //
            pointArray = pointArray.concat(ply.getPath());
